'use client';

import React, { useState } from "react";
import SideBar from "../../../components/SideBar/page";
import { Search, Bell } from "lucide-react";
import Image from "next/image";

const VolunteerManagement = () => {
  const [search, setSearch] = useState("");
  const [volunteers, setVolunteers] = useState([
    { id: 1, name: "John Doe", email: "john@example.com", role: "Collector", status: "Active" },
    { id: 2, name: "Jane Smith", email: "jane@example.com", role: "Recycler", status: "Inactive" },
  ]);
  const [newVolunteer, setNewVolunteer] = useState({ name: "", email: "", role: "Collector", status: "Active" });
  const [isEdit, setIsEdit] = useState(false);
  const [editId, setEditId] = useState<number | null>(null);

  const toggleStatus = (id: number) => {
    setVolunteers(
      volunteers.map((v) =>
        v.id === id ? { ...v, status: v.status === "Active" ? "Inactive" : "Active" } : v
      )
    );
  };

  const addVolunteer = () => {
    if (newVolunteer.name && newVolunteer.email) {
      setVolunteers([...volunteers, { ...newVolunteer, id: Date.now() }]);
      setNewVolunteer({ name: "", email: "", role: "Collector", status: "Active" });
    }
  };

  const editVolunteer = (id: number) => {
    const volunteerToEdit = volunteers.find((v) => v.id === id);
    if (volunteerToEdit) {
      setNewVolunteer(volunteerToEdit);
      setEditId(id);
      setIsEdit(true);
    }
  };

  const updateVolunteer = () => {
    setVolunteers(volunteers.map((v) => (v.id === editId ? { ...v, ...newVolunteer } : v)));
    setNewVolunteer({ name: "", email: "", role: "Collector", status: "Active" });
    setIsEdit(false);
    setEditId(null);
  };

  const deleteVolunteer = (id: number) => {
    setVolunteers(volunteers.filter((v) => v.id !== id));
  };

  const filteredVolunteers = volunteers.filter((v) =>
    v.name.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <div className="min-h-screen bg-gray-100 flex">
      <SideBar />
      <div className="flex-1">
        <header className="bg-white shadow p-4 flex justify-between items-center">
          <div className="relative">
            <input
              type="search"
              placeholder="Search..."
              className="pl-3 pr-10 py-2 border rounded-md w-64"
              value={search}
              onChange={(e) => setSearch(e.target.value)}
            />
            <Search className="w-5 h-5 absolute right-3 top-2.5 text-gray-400" />
          </div>
          <div className="flex items-center gap-4">
            <button className="relative">
              <Bell className="w-6 h-6 text-gray-600" />
            </button>
            <div className="flex items-center gap-2">
              <Image src="/profile.jpg" alt="Admin" width={32} height={32} className="rounded-full" />
              <p className="text-sm font-medium">Admin</p>
            </div>
          </div>
        </header>
        <main className="p-6">
          <h1 className="text-2xl font-semibold mb-4">Volunteer Management</h1>
          <div className="bg-white shadow-md rounded-lg p-4 mb-6">
            <input type="text" placeholder="Name" className="border rounded-lg p-2 mb-4 w-full" value={newVolunteer.name} onChange={(e) => setNewVolunteer({ ...newVolunteer, name: e.target.value })} />
            <input type="email" placeholder="Email" className="border rounded-lg p-2 mb-4 w-full" value={newVolunteer.email} onChange={(e) => setNewVolunteer({ ...newVolunteer, email: e.target.value })} />
            <button onClick={isEdit ? updateVolunteer : addVolunteer} className="bg-green-600 text-white px-4 py-2 rounded-lg hover:bg-green-700">{isEdit ? "Update Volunteer" : "Add Volunteer"}</button>
          </div>
          <div className="bg-white shadow-md rounded-lg p-4">
            <table className="w-full border-collapse">
              <thead>
                <tr className="bg-gray-200 text-left">
                  <th className="p-3">Name</th>
                  <th className="p-3">Email</th>
                  <th className="p-3">Role</th>
                  <th className="p-3">Status</th>
                  <th className="p-3">Actions</th>
                </tr>
              </thead>
              <tbody>
                {filteredVolunteers.map((volunteer) => (
                  <tr key={volunteer.id} className="border-t">
                    <td className="p-3">{volunteer.name}</td>
                    <td className="p-3">{volunteer.email}</td>
                    <td className="p-3">{volunteer.role}</td>
                    <td className={`p-3 font-semibold ${volunteer.status === "Active" ? "text-green-600" : "text-red-600"}`}>{volunteer.status}</td>
                    <td className="p-3 flex gap-2">
                      <button onClick={() => toggleStatus(volunteer.id)} className="bg-blue-500 text-white px-3 py-1 rounded-lg">Toggle</button>
                      <button onClick={() => editVolunteer(volunteer.id)} className="bg-yellow-500 text-white px-3 py-1 rounded-lg">Edit</button>
                      <button onClick={() => deleteVolunteer(volunteer.id)} className="bg-red-500 text-white px-3 py-1 rounded-lg">Delete</button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </main>
      </div>
    </div>
  );
};

export default VolunteerManagement;
